import React, { Component } from "react";
import "./about.css";

class About extends Component {
  render() {
    return (
      <div id="about">
        <div className="detail-page-main-about">
          <iframe
            className="detail-page-main-about-map"
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3172.5892358788415!2d-121.863388448964!3d37.32855654537962!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x808e332b461f330b%3A0xbb2e2545160245e0!2sKelly%20Park%20Medical%20Clinic%20Inc!5e0!3m2!1sen!2sus!4v1576793326662!5m2!1sen!2sus"
            title="map"
            frameBorder="0"
            allowFullScreen=""
          ></iframe>
          <div className="detail-page-main-about-info">
            <h1 className="detail-page-main-about-info-h1">Address</h1>
            <address className="detail-page-main-about-info-p">
              Kelly Park Medical Clinic Inc <br /> 749 Story Rd # 20, <br />
              San Jose, CA 95122
            </address>
            <h1 className="detail-page-main-about-info-h1">Contacts</h1>
            <p className="detail-page-main-about-info-p">+1 (408) 794-2088</p>

            <ul className="detail-page-main-about-info-list">
              <li className="detail-page-main-about-info-list-li">
                Make appointment
              </li>
              <li className="detail-page-main-about-info-list-li">
                Ask for informations
              </li>
              <li className="detail-page-main-about-info-list-li">
                Ask any questions
              </li>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default About;
