import React, { Component } from "react";
import "./certified.css";
import banner from "./pictures/backupBanner.jpg";

class Certified extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  goBack() {
    let card = document.querySelector(".detail-page-main-certified-wrapper");
    card.scrollLeft -= card.clientWidth;
  }

  goNext() {
    let card = document.querySelector(".detail-page-main-certified-wrapper");
    card.scrollLeft += card.clientWidth;
  }

  render() {
    return (
      <div id="certified">
        <div className="detail-page-main-certified">
          <h1 className="detail-page-main-certified-h1">Certified</h1>
          <div className="detail-page-main-certified-wrapper">
            <div className="detail-page-main-certified-card">
              <img
                className="detail-page-main-certified-card-picture"
                src={banner}
                alt="certified"
              />
            </div>
            <div className="detail-page-main-certified-card">
              <img
                className="detail-page-main-certified-card-picture"
                src={banner}
                alt="certified"
              />
            </div>
            <div className="detail-page-main-certified-card">
              <img
                className="detail-page-main-certified-card-picture"
                src={banner}
                alt="certified"
              />
            </div>
            <div className="detail-page-main-certified-card">
              <img
                className="detail-page-main-certified-card-picture"
                src={banner}
                alt="certified"
              />
            </div>
            <div className="detail-page-main-certified-card">
              <img
                className="detail-page-main-certified-card-picture"
                src={banner}
                alt="certified"
              />
            </div>
            <div className="detail-page-main-certified-card">
              <img
                className="detail-page-main-certified-card-picture"
                src={banner}
                alt="certified"
              />
            </div>
          </div>

          <div className="detail-page-main-certified-controll">
            <button
              onClick={this.goBack}
              className="detail-page-main-certified-controll-button"
            >
              <i className="fas fa-long-arrow-alt-left"></i>
              Prev
            </button>
            <button
              onClick={this.goNext}
              className="detail-page-main-certified-controll-button"
            >
              Next
              <i className="fas fa-long-arrow-alt-right"> </i>
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default Certified;
