import React, { Component } from "react";
import "./services.css";
import banner from "./pictures/banner.jpg";

class Services extends Component {
  render() {
    return (
      <div id="services">
        <div className="detail-page-main-services">
          <h1 className="detail-page-main-services-h1">Our Services</h1>
          <div className="detail-page-main-services-wrapper">
            <div className="detail-page-main-services-wrapper-box">
              <img
                className="detail-page-main-services-wrapper-box-picture"
                src={banner}
                alt="services"
              />
              <p className="detail-page-main-services-wrapper-box-servicesname">
                Disease prevention
              </p>
            </div>
            <div className="detail-page-main-services-wrapper-box">
              <img
                className="detail-page-main-services-wrapper-box-picture"
                src={banner}
                alt="services"
              />
              <p className="detail-page-main-services-wrapper-box-servicesname">
                Health Maintenance
              </p>
            </div>
            <div className="detail-page-main-services-wrapper-box">
              <img
                className="detail-page-main-services-wrapper-box-picture"
                src={banner}
                alt="services"
              />
              <p className="detail-page-main-services-wrapper-box-servicesname">
                Counseling
              </p>
            </div>
            <div className="detail-page-main-services-wrapper-box">
              <img
                className="detail-page-main-services-wrapper-box-picture"
                src={banner}
                alt="services"
              />
              <p className="detail-page-main-services-wrapper-box-servicesname">
                Patient Education
              </p>
            </div>
            <div className="detail-page-main-services-wrapper-box">
              <img
                className="detail-page-main-services-wrapper-box-picture"
                src={banner}
                alt="services"
              />
              <p className="detail-page-main-services-wrapper-box-servicesname">
                Diagnosis
              </p>
            </div>
            <div className="detail-page-main-services-wrapper-box">
              <img
                className="detail-page-main-services-wrapper-box-picture"
                src={banner}
                alt="services"
              />
              <p className="detail-page-main-services-wrapper-box-servicesname">
                Treatment
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Services;
