import React, { Component } from "react";
import "./footer.css";

class Footer extends Component {
  render() {
    return (
      <div>
        <footer className="detail-page-main-footer">
          <h1 className="detail-page-main-footer-h1">
            <i className="fas fa-clinic-medical"></i> Kelly Park Clinic
          </h1>
          <div className="detail-page-main-footer-divider">
            <div className="detail-page-main-footer-divider-left">
              <h1 className="detail-page-main-footer-divider-h1">
                <i className="fas fa-user-md"></i> Dr. PETER LINH NGUYEN
              </h1>
            </div>
            <div className="detail-page-main-footer-divider-right">
              <h1 className="detail-page-main-footer-divider-h1">Schedule</h1>
              <ol className="detail-page-main-footer-divider-right-ol">
                <li className="detail-page-main-footer-divider-right-ol-li">
                  Mon: 8:00AM to 9:00PM
                </li>
                <li className="detail-page-main-footer-divider-right-ol-li">
                  Tue: 8:00AM to 9:00PM
                </li>
                <li className="detail-page-main-footer-divider-right-ol-li">
                  Wed: 8:00AM to 9:00PM
                </li>
                <li className="detail-page-main-footer-divider-right-ol-li">
                  Thu: 8:00AM to 9:00PM
                </li>
                <li className="detail-page-main-footer-divider-right-ol-li">
                  Fri: 8:00AM to 9:00PM
                </li>
                <li className="detail-page-main-footer-divider-right-ol-li">
                  Sat: 8:00AM to 9:00PM
                </li>
                <li className="detail-page-main-footer-divider-right-ol-li">
                  Sun: Closed
                </li>
              </ol>
            </div>
          </div>
        </footer>
      </div>
    );
  }
}

export default Footer;
