import React, { Component } from "react";
import "./navbar.css";

class Navbar extends Component {
  constructor(props) {
    super(props);

    this.state = {
      mobileNav: false
    };
    this.switchNav = this.switchNav.bind(this);
  }

  switchNav() {
    this.setState({ mobileNav: !this.state.mobileNav });
  }

  render() {
    return (
      <header>
        <nav className="nav-bar-mobile">
          <ul className="nav-bar-mobile-ul">
            <li className="nav-bar-mobile-ul-li">
              <i className="fas fa-clinic-medical"></i> Kelly Park Clinic
            </li>
            <li className="nav-bar-mobile-ul-li" onClick={this.switchNav}>
              <i className="fas fa-bars"></i>
            </li>
          </ul>
        </nav>
        <nav className={this.state.mobileNav ? "nav-bar show" : "nav-bar hide"}>
          <ul className="nav-bar-list">
            <a className="nav-bar-list-a" href="#a" onClick={this.switchNav}>
              <li className="nav-bar-list-li nav-bar-list-home">
                <i className="fas fa-clinic-medical"></i> Kelly Park Clinic
              </li>
            </a>
            <a
              className="nav-bar-list-a"
              href="#banner"
              onClick={this.switchNav}
            >
              <li className="nav-bar-list-li nav-bar-list-li-home">
                <i className="fas fa-arrow-up"></i> Home
              </li>
            </a>
            <a
              className="nav-bar-list-a"
              href="#doctor"
              onClick={this.switchNav}
            >
              <li className="nav-bar-list-li nav-bar-list-li-doctor">
                <i className="fas fa-user-md"></i> Doctor
              </li>
            </a>
            <a
              className="nav-bar-list-a"
              href="#services"
              onClick={this.switchNav}
            >
              <li className="nav-bar-list-li nav-bar-list-li-services">
                <i className="fas fa-clipboard-list"></i> Services
              </li>
            </a>
            <a
              className="nav-bar-list-a"
              href="#certified"
              onClick={this.switchNav}
            >
              <li className="nav-bar-list-li nav-bar-list-li-cert">
                <i className="fas fa-certificate"></i> Certified
              </li>
            </a>

            <a
              className="nav-bar-list-a"
              href="#about"
              onClick={this.switchNav}
            >
              <li className="nav-bar-list-li nav-bar-list-li-about">
                <i className="fas fa-comment-dots"></i> About
              </li>
            </a>
          </ul>
        </nav>
      </header>
    );
  }
}

export default Navbar;
