import React, { Component } from "react";
import "./banner.css";

class Banner extends Component {
  render() {
    return (
      <div>
        <div id="banner" className="detail-page-main-picture">
          <h1 className="detail-page-main-picture-h1">Dr. PETER LINH NGUYEN</h1>
          <h1 className="detail-page-main-picture-h1">Family Medicine</h1>
          <h1 className="detail-page-main-picture-h1">Care with compassion</h1>
          <a className="detail-page-main-picture-button-a" href="#about">
            <button className="detail-page-main-picture-button">
              More information
            </button>
          </a>
        </div>
        <div className="detail-page-main-things">
          <div className="detail-page-main-things-card">
            <h1 className="detail-page-main-things-card-h1">
              Experienced Staff
            </h1>
            <p className="detail-page-main-things-card-p">
              Our staffs are waiting to help you to make your visit go as
              smoothly as possible.
            </p>
          </div>
          <div className="detail-page-main-things-card">
            <h1 className="detail-page-main-things-card-h1">Compassion</h1>
            <p className="detail-page-main-things-card-p">
              Our goal has always been patients health first and foremost.
            </p>
          </div>
          <div className="detail-page-main-things-card">
            <h1 className="detail-page-main-things-card-h1">One Big Family</h1>
            <p className="detail-page-main-things-card-p">
              We treat our patients like a family member.
            </p>
          </div>
        </div>
      </div>
    );
  }
}
export default Banner;
